<template>
  <div class="detail-box">
    <div class="header">
      <van-image :src="productsDetail.coverImg">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
      </van-image>
      <div class="describe">
        <h3 class="name">{{ productsDetail.name }}</h3>
        <p class="quantity">数量：{{ productsDetail.quantity }}</p>
        <p class="price">价格：{{ (productsDetail.price / 100).toFixed(2) }}</p>
      </div>
    </div>
    <div class="content" v-html="productsDetail.content"></div>
  </div>
</template>

<script>
import {reqProductsDetail} from '@/api'

export default {
  name: 'Detail',
  data() {
    return {
      productsDetail: {}
    }
  },
  created() {
    reqProductsDetail(this.$route.query.id).then(res => {
      this.productsDetail = res
    })
  }
}
</script>

<style lang="less" scoped>
.detail-box {
  .describe {
    margin: 20px auto;
    width: 90%;
    border: 1px solid #ccc;
    padding: 20px;
    box-sizing: border-box;
    h3 {
      margin: 0 0 10px;
    }
    p {
      margin: 0 0 6px;
    }
  }
}
</style>
<style lang="less">
p {
  margin: -1px 0 0;
  font-size: 0;
  img {
    width: 100%;
    height: 100%;
  }
}
</style>
